import {ArrayChange} from "diff";

export type FunOrVal<T> = T | (() => T)
export type PPair<T> = [h: T, t?: T]
export type Pair<T> = Required<PPair<T>>

export interface Change extends Record<Change.Type, boolean> {
}

export class Change extends Array<string> implements ArrayChange<string> {

    // opts out of the merging step and omits a leading space
    noMerge: boolean;
    readonly value: string[] = this;
    type: Change.Type | null;

    clone(mods: Partial<Change>): Change
    clone(type: Change.Type.Union, noMerge?: boolean): Change
    clone(): Change
    clone(...args: Exclude<ChangeConstructorParams, string[]> extends [any, ...infer X] ? X : never) {
        return new Change(this, ...args);
    }

    // merges two changes together. hopefully they are the same type and mergeable.
    merge(v: Change | Null) {
        if (!v || this.noMerge || v.noMerge || !this.sameType(v)) return null;
        this.push(...v);
        return this;
    }

    // construct via super
    constructor(...items: string[])
    // clone via another object with overrides
    constructor(...args: ChangeObjectParams);
    // construct via providing properties sequentially
    constructor(...args: ChangeLiteralParams);
    constructor(...args: ChangeConstructorParams);
    constructor(...args: ChangeConstructorParams) {
        let items: string[] | undefined, type: Change.Type.Union | undefined, noMerge: boolean | undefined;
        const [arg0, arg1, arg2] = args;
        if (typeof arg0 === 'object') {
            if (typeof arg2 === 'string') throw TypeError();
            if ((arg2 !== undefined && arg1 === undefined) || Change.Type.includes(arg1)) {
                // ChangeLiteralParams
                type = arg1;
                noMerge = arg2;
            } else if (arg1) {
                // ChangeObjectParams
                if (typeof arg1 === 'string') throw new TypeError(`Invalid Type: ${arg1}`);
                ({value: items, type, noMerge} = arg1)
            }
            items ??= ('value' in arg0 && arg0.value) || (Array.isArray(arg0) && arg0) || [];
            if (type === undefined) {
                type = 'type' in arg0 && arg0.type !== undefined ? arg0.type : Change.Type.values.find(t =>
                    (arg0 as Partial<Record<Change.Type, boolean>>)[t])
            }
            if ('noMerge' in arg0) noMerge ??= arg0.noMerge
        } else {
            // none of the other combinations have a string in the first spot.
            items = args as string[]
        }
        super(...items)
        this.type = type ?? null;
        this.noMerge = noMerge ?? false;
        // todo is this even worth it
        Object.defineProperties(this, Object.fromEntries(Change.Type.values.map((type) => {
            const get = () => this.type === type;
            return [type, {
                get, set: (set: boolean) => {
                    if (get()) this.type = set ? type : null;
                },
            } satisfies PropertyDescriptor]
        })))
    }

    splice(start?: number, deleteCount = start !== undefined ? Infinity : 0, ...items: string[]): Change {
        const a = super.splice(start ?? 0, deleteCount, ...items);
        return a instanceof Change ? a : new Change(a, this);
    }

    sameType(other: Change) {
        return this.type === other.type;
    }

    typeIs(...types: Change.Type.Union[]) {
        return types.includes(this.type)
    }

    get hasLeadingSpace() {
        return this[0][0] === ' '
    }

    get isSpace() {
        return this.length === 1 && this.hasLeadingSpace
    }

    get count() {
        let i = 0;
        for (const string of this) if (string !== ' ') i++;
        return i;
    }
}

type ChangeConstructorParams =
    string[] |
    ChangeLiteralParams |
    ChangeObjectParams
type ChangeStandIn = Array<string> | Partial<Change> | ArrayChange<string>
type ChangeLiteralParams = [value: ChangeStandIn, type?: Change.Type.Union, noMerge?: boolean]
type ChangeObjectParams = [value: ChangeStandIn, mods?: Partial<Change>]

export module Change {
    // representations of the different change states
    export enum Type {
        REM = "removed",
        ADD = "added",
        COM = "comment",
    }

    export module Type {
        export const values = [Type.REM, Type.ADD, Type.COM] as const

        // used for de-parsing
        export const keys = values.reduce((p, type) => ({...p, [type]: type.slice(0, 3)}), {}) as Record<Type, string>;

        // all types, with null standing in for no change
        export type Union = Type | null;

        export function includes<T>(string: T): string is T & (T extends null ? Union : Type) {
            return string === null || values.includes(string as any);
        }
    }
}

export type ChangeIter<T extends Change = Change> = T[] | Generator<T> | Iterable<T>;

type Null = undefined | null
// noinspection SpellCheckingInspection
type Falsey = Null | false | ''

// wraps a value into an array. If the value is falsey, an empty array is returned.

export const toArray = <T>(t: T) => (Array.isArray(t) ? t : t ? [t] : []) as AsArray<Exclude<T, Falsey>>
export type AsArray<T> = ArrayLikeNotString<T> | NotArrayLike<T>[]
type ArrayLikeNotString<T> = Extract<Exclude<T, string>, ArrayLike<any>>
type NotArrayLike<T> = Extract<T, string> | Exclude<T, ArrayLike<any>>
