import {AsArray, Change, ChangeIter, FunOrVal, Pair, PPair, toArray} from "./types";
import ChangeType = Change.Type;

class FormatToggles implements Format.Toggles {
    symbols: Exclude<Format.Toggles["symbols"], undefined> = {
        replaceAtEnds: false,
        omitRemoved: false,
        overrideType: false,
    };
    paragraph = false;
    formatTags = true;
    colors?: Record<ChangeType, string>

    constructor(toggles?: Format.Toggles) {
        Object.assign(this, toggles);
    }
}

const isWord = /^\w+$/

class SymbolToken {
    constructor(
        public readonly symbol: string,
        public readonly value?: string,
    ) {
    }
}

export abstract class Format<Tag, Formatted = Tag> extends FormatToggles {
    [ChangeType.ADD]?: Tag
    [ChangeType.REM]?: Tag
    [ChangeType.COM]?: Tag

    formatTag(t: ChangeType.Union = null, isSymbol = false) {
        if (isSymbol) t = Change.Type.COM; // override to get comment's tags for symbol markers
        return this.formatTags || t === ChangeType.REM ? t && this[t] as Tag : null;
    }

    isSymbol(v?: string): string | undefined {
        switch (v) {
            case '.':
                return 'P';
            case ',':
                return 'C';
            case ';':
                return 'SC';
        }
    }

    extractSymbols(change: Change) {
        const {value} = change;
        if (!this.symbols || (!change.added && !change.removed) || (!this.symbols.replaceAtEnds && change.value.length > 1))
            return [];
        return [value.at(0), this.symbols.replaceAtEnds && value.length > 1 ? value.at(-1) : undefined].map((char) => {
            const symbol = this.isSymbol(char);
            return symbol && new SymbolToken(`(${change.added ? 'A' : 'R'}${symbol})`, this.symbols!.omitRemoved && change.removed ? undefined : char);
        }, this) as [h?: SymbolToken, t?: SymbolToken]
    };

    beforeJoin(diff: Formatted[], toAdd: Formatted[]) {
    }

    protected abstract formatChange(change: Change, tags?: Tag): Formatted | Formatted[];

    format(changes: Readonly<ChangeIter>) {
        const diff = Array<Formatted>()
        // number of changes processed.
        let n = 0;

        let last: Partial<{ type: ChangeType.Union, token: string }> = {}
        const baseAdd = (change: Change, isSymbol?: boolean) => {
            const {value, type} = change;
            if (!value.length) return;
            last.token = value.at(-1);
            if (type) for (let i = 0; i < value.length; i++) if (value[i] === '\n') {
                value[i] = `${this.paragraph ? '¶' : ''}${change.removed ? this.paragraph ? '' : ' ' : value[i]}`;
            }
            const formatted = toArray(this.formatChange(change, this.formatTag(type, isSymbol) ?? undefined)) as Formatted[]
            this.beforeJoin(diff, formatted);
            diff.push(...formatted);
        }

        // record of a trailing space from the previous change. Avoids space duplication
        let trailing: any
        // adds a space specifically. todo give this special handling so it can be easily identified as a non-token.
        const addSpace = (space: Partial<Change> = {}) => baseAdd(new Change({value: [' '], type: null, ...space}))
        for (const c of changes) {
            const change = c.clone();
            const {type, value} = change;
            if (!value.length || (change.isSpace && change.removed)) {
                // this is the step that breaks the count.
                last = {type, token: value.at(0)}
                continue;
                // omit removed spaces and stuff without any value, but log them regardless to ensure consistent behavior.
            }
            const add = (value: string | SymbolToken | string[] | undefined) => {
                let {type} = change
                let isSymbol = false
                if (value instanceof SymbolToken) {
                    // special punctuation mark logic
                    isSymbol = true;
                    if (value.value) add(value.value);
                    // ensure leading space is added
                    if (diff.length) addSpace();
                    value = value.symbol;
                    if (this.symbols?.overrideType) type = ChangeType.COM;
                }
                baseAdd(new Change(change, {value: toArray(value), type}), isSymbol)
            }
            // fixme this should not be needed; it should be handled in initial processing
            // fixing this also would make this much more robust.
            // if there was already a leading space, treat that as it being included.
            const leadingSpace = value[0] === ' ';
            if (trailing || (leadingSpace && value.length > 1)) {
                // insert a space before if either the last one was supposed to have a space, or if this one has a leading space.
                if (leadingSpace) value.shift();
                else trailing = undefined; // preserve until it's actually needed
                addSpace();
            } else if (diff.length && !c.noMerge && (
                type === ChangeType.COM || ((
                    // it's plainly obvious that two word characters should have a space between them.
                    // this also catches weird edge cases. While they shouldn't exist to begin with, they also don't really matter.
                    last?.type === ChangeType.COM || ((
                        // edge case to ensure replacing the very first token results in a proper space.
                        n === 1 && type === ChangeType.ADD && last?.type === ChangeType.REM
                    ) && last.token?.match(isWord))) && isWord.test(value[0]))
            )) addSpace();
            // only split off the space if it's actually a trailing space.
            // there shouldn't be a case where the fact this can stack should ever matter, because otherwise they would have already been merged...
            if (value.length > 1 && value.at(-1) === ' ') trailing = value.pop();
            const [h, t] = this.extractSymbols(change);
            if (h) {
                add(h)
                value.shift();
                if (value[0] === ' ') {
                    addSpace();
                    value.shift();
                }
            }
            if (t) value.pop();
            if (value.length) add(value);
            if (t) add(t);
            last.type = type;
            if (last.token === '\n') {
                // reset first space consideration on newline
                n = 0;
            } else {
                ++n;
            }
        }
        return diff;
    }
}

export module Format {
    export interface Toggles {
        // render symbols as special cases. unsetting this disables this behavior.
        symbols?: {
            // just render the marker when marking removed symbols
            omitRemoved?: boolean,
            // results in more markers. A lot more.
            replaceAtEnds?: boolean,
            // passes symbols as comments directly, causing them to be treated as comments in all respects (typically just changes the color, but can also affect designations)
            overrideType?: boolean
        } | null;
        // show paragraph markers
        paragraph?: boolean;
        // show format tags
        formatTags?: boolean;
        // used for some that use this.
        colors?: Record<ChangeType, string>;
    }

    export const Toggles = FormatToggles
}

abstract class TagFormatter<M = string> extends Format<string> {
    protected abstract matchHead: (h: string) => M | boolean | undefined | null
    protected matchTail?: (match: NonNullable<M>, h: string[], t: string) => string | boolean

    override beforeJoin(...[diff, toAdd]: Pair<string[]>) {
        const space = (diff.at(-1) === ' ' && diff.pop()) || undefined; // ignore spaces between tokens
        while (diff.length) {
            const head = toAdd[0]
            const match = this.matchHead(head);
            if (!match) break; // no match on the opening tag
            const tail = diff.at(-1)!,
                m = match === true ? this.matchHead(tail) as boolean :
                    this.matchTail ? this.matchTail(match, diff.splice(0, -1), tail) :
                        head;
            if (typeof m == 'boolean' ? !m : m !== tail) break; // no match on the closing tag
            toAdd.shift();
            diff.pop();
        }
        if (space) toAdd.unshift(space);
    }

    protected abstract getTags(type: Change.Type): string[];

    protected makeTags(tag: string): readonly [open: string, close?: string] {
        return [tag];
    }

    protected formatChange({type, value}: Change, tags?: string) {
        if (!type) return value;
        return toArray(tags).concat(this.getTags(type))
            .reduce((p, tag) => del(p, ...this.makeTags(tag)), value)
    }
}

export class BBCode extends TagFormatter<RegExpExecArray> {

    private static TagMatcher = RegExp.prototype.exec.bind(/^\[([^=]+)(=?(.*))]$/)

    protected getTags(type: Change.Type) {
        const color = this.colors?.[type];
        return toArray(color && `color=${color}`).concat(
            'b' // standard bolding
        )
    }

    protected makeTags(tag: string) {
        return [`[${tag}]`, `[/${tag.split('=', 1)[0]}]`] as const;
    }

    matchHead = BBCode.TagMatcher;
    matchTail = ([, tag, value]: RegExpExecArray, diff: string[], tail: string) => {
        const closing = `[/${tag}]`;
        if (closing !== tail) return false;
        if (!value) return true;
        // ensure the value started is the same
        for (let i = 1; i < diff.length; i++) {
            const t = diff.at(-i - 1);
            if (!t || t === closing) break;
            const m = this.matchHead(t);
            if (m?.[1] === tag) return m[2] === value;
        }
        return false;
    }

    [ChangeType.ADD] = 'U';
    [ChangeType.REM] = 'S';
    [ChangeType.COM] = 'I';
}

// technically the old Markdown implementation, doesn't bother to ensure the resulting markdown actually formats correctly. Used for testing with minor changes.
export class MarkdownUnsafe extends TagFormatter {

    matchHead = (head: string) => /[*_]$/.test(head) ? head : undefined

    protected override getTags() {
        return ['**'];
    }

    [ChangeType.ADD] = '__';
    [ChangeType.COM] = '*';
    [ChangeType.REM] = '~~';
}

// actual markdown formatter, formats with bold/italics/underline and escapes any character that could possibly be problematic.
export class Markdown extends MarkdownUnsafe {
    protected formatChange(change: Change, tags?: string) {
        // escape potentially problematic characters
        if (change.isSpace) tags = undefined; // this won't render correctly, ever. todo make some indicator of this...
        let last = 0;
        let res: string[] = []
        change = change.clone();
        for (let i = 0; i < change.length; i++) {
            if (change.type === ChangeType.ADD && change[i].at(this.paragraph ? 1 : 0) === '\n') {
                if (last < i) res.push(...super.formatChange(change.clone({value: change.slice(last, i)}), tags));
                last = i + 1;
                res.push(change[i]); // include by itself
            }
            // escape all potentially problematic characters
            else
                change[i] = change[i].replaceAll(this.escaped, '\\$&')
        }
        res.push(...super.formatChange(change.clone({value: change.slice(last)}), tags));
        return res;
    }

    private _escaped?: RegExp
    private get escaped() {
        if (this._escaped) return this._escaped;
        const set = new Set(ChangeType.values.map(i => this[i].at(0)))
        set.delete(undefined);
        set.delete('*');
        set.add('\\*');
        return this._escaped = new RegExp([...set.values()].join('|'), 'g')
    }
}

// utility methods
function del<T>(value: T | AsArray<T>, ...d: PPair<FunOrVal<Exclude<T, CallableFunction>>>) {
    if (!value) return [];
    if (d.length === 1) d[1] = d[0];
    const [h, t] = (d as Pair<FunOrVal<T>>)
        .map(d => typeof d === 'function' ? (d as () => T)() : d)
    return [h, value, t].flatMap(toArray)
}