import React, {useState, useMemo, useCallback} from 'react';
import {useCopyToClipboard, useLocalStorage, useWindowSize} from 'react-use'

import Panel from "./Panel";
import {BoxHeader, useDiff, Versions} from "./Diff";
import './App.css';
import {Input} from "./utils";
import {Change} from "./gpdif/types";
import ChangeType = Change.Type
import {BBCode, Markdown} from "./gpdif/diff-format";
import {useToggles} from "./Toggles";
import {Footer} from "./Footer";


// I'm aware this is dumb.

function rgbToHex(hex: string) {
    return hex.match(/rgb\((\d+),(\d+),(\d+)\)/)!.slice(1)
        .reduce((p, c) => p + parseInt(c).toString(16), '#')
}

type ColorMap = Record<ChangeType, string>
const defaultColors = function () {
    const keys = [Change.Type.ADD, Change.Type.REM, Change.Type.COM] as const
    const colors = ['rgb(41,105,176)', 'rgb(226,80,65)', 'rgb(65,168,95)'];
    return Object.assign(
        keys.reduce((p, t, i) =>
            Object.assign(p, {[t]: rgbToHex(colors[i])}), {} as ColorMap
        ), {keys}
    )
}()

function App() {
    const [colors, resetColors] = useColors()
    const [toggles, setToggles] = useToggles(colors)
    const [override, setOverride] = React.useState<{ [P in 'style' | 'color']?: boolean }>()
    if (override?.style !== undefined) toggles.formatTags = override.style
    const diffState = useDiff(toggles),
        {diff, formatter: previewFormat, clear, swap, colorLegend} = diffState;
    const formatters = useMemo(function () {
        return {
            BBCode: new BBCode(toggles),
            Markdown: new Markdown(toggles),
        } as const;
    }, [toggles]);
    type Format = keyof typeof formatters;
    const [outputType, setOutputType] = useState<Format>("BBCode")
    // the markdown (really discord-compatible) implementation has no way to show colors, so font must be enabled
    const format = useCallback<NonNullable<Versions.Props['export']>>(function format(changes) {
        return formatters[outputType].format(changes).join('')
    }, [formatters, outputType])
    React.useEffect(
        () => {
            const [override, legend] =
                outputType === 'Markdown' ? [{style: true, color: false}, false]
                    : []
            setOverride(override)
            colorLegend.checked = legend;
            // eslint-disable-next-line react-hooks/exhaustive-deps
        }, [outputType]);

    const outputCode = useMemo(() => format(diff), [format, diff])

    const OutputFormat = useCallback(function OutputFormat(
        {name, ...props}:
            Partial<Omit<Parameters<typeof Input>[0], 'state' | 'type' | 'checked' | 'value'>>
            & { name: Format }
    ) {
        return <Input
            name={OutputFormat.name} type={"radio"}
            checked={outputType === name}
            state={[name, setOutputType]}
            {...props}>{name}</Input
        >
    }, [outputType])

    const [_clipboard, copyToClipboard] = useCopyToClipboard();
    const [oldHidden, hideOld] = diffState.hideOld

    const Color = useCallback((t: ChangeType) => {
        const colorTitles = {
            [ChangeType.ADD]: "additions",
            [ChangeType.REM]: "removals",
            [ChangeType.COM]: "comments (text in the new box enclosed with {})"
        };
        const Tag = previewFormat.formatTag(t)!;
        return <Input type='color' className={'color'} key={t}
                      name={`color ${t}`}
                      title={`Color used for ${colorTitles[t]}`}
                      state={[colors[t], (v: string) => colors[t] = v]}
        ><Tag id={`${t}-preview`}>{t.substring(0, 3)}</Tag></Input>;
    }, [colors, previewFormat])

    return <div className="App" style={useWindowSize()}>
        <main>
            {/* diff stuff */}
            <Versions export={format} useDiff={diffState}/>
            <div id='row2'>
                <Panel name={'options'} id={'panel'} legend={<h2>Options</h2>}>
                    {/* this div keeps these on their own line for the most part. */}
                    <div>
                        <Panel name={'boxControls'} legend={'Box Controls'}>
                            <button name={'toggleOld'}
                                    title={`${oldHidden ? 'Show' : 'Hide'} the left box`}
                                    onClick={() => hideOld()}
                            >
                                {oldHidden ? 'Show' : 'Hide'} Old
                            </button>
                            <div>
                                <button {...clear}
                                        title={'clear contents of both boxes'}
                                        onClick={() => window.confirm('Clear both boxes?') && clear()}>
                                    Clear Boxes
                                </button>
                                <button {...swap} onClick={swap}>Swap Boxes</button>
                            </div>
                        </Panel>
                        {/* colors */}
                        <Panel name={'colors'} title={"Change output colors"}>
                            <div id='colors'>{defaultColors.keys.map(Color)}</div>
                            <button id='reset-color'
                                    onClick={() => resetColors()}
                                    title={'reset colors to defaults'}
                                    disabled={ChangeType.values.every(type => colors[type] === defaultColors[type])}>
                                Reset Color
                            </button>
                        </Panel>
                    </div>
                    <Panel name={'format'} legend={'Formatting'}>
                        <Panel
                            name={'symbols'}
                            onHide={useCallback((hidden: boolean) => setToggles({symbols: !hidden}), [setToggles])}
                            hidden={!toggles.symbols}
                            legend={<>
                                Punctuation Markers
                                { /* "fake" checkbox*/}
                                <input className={'inputLabel'} name={'punctuation'} type={'checkbox'}
                                       tabIndex={-1} readOnly={true} onClick={() => false}
                                       checked={!!toggles.symbols}
                                />
                            </>}
                            legendProps={{
                                title: "Show (AC/RC), (AP,RP), and (ASC/RSC) for ',', '.', and ';', respectively," +
                                    "and disable extra formatting for these cases" +
                                    "\n" +
                                    "This makes checks harder to implement cleanly," +
                                    "but makes it much easier to identify certain small markers that would be otherwise very easy to miss." +
                                    "\n\n" +
                                    "This feature will likely be expanded in the future to be more customizable.",
                                className: 'inputLabel'
                            }}>
                            <Input
                                name={'omitRemoved'}
                                type={'checkbox'}
                                disabled={!toggles.symbols}
                                title={"Write '(RC)' instead of ', (RC)'\n" +
                                    "This tends to be easier to implement."}
                                state={[
                                    toggles.symbols?.omitRemoved,
                                    (symOmitRemoved: boolean) => setToggles({symOmitRemoved})
                                ]}>Omit Removed Punctuation</Input>
                            <Input
                                name={'replaceAtEnds'}
                                title={"Prevents output from marking punctuation that is connected to other changes\n" +
                                    "This tends to yield shorter output with a minimal loss in visibility, but every once in a while can yield confusing output."}
                                type={'checkbox'}
                                disabled={!toggles.symbols}
                                state={[
                                    // inverted
                                    !toggles.symbols?.replaceAtEnds,
                                    (v: boolean) => setToggles({symReplaceAtEnds: !v})
                                ]}
                            >Only Isolated</Input>
                            <Input name='overrideType'
                                   title={'By default, marks will use the color of the change they were derived from.'
                                       + '\n'
                                       + 'This instead makes them proper comments.'
                                   }
                                   type="checkbox"
                                   state={[
                                       toggles.symbols?.overrideType,
                                       (v: boolean) => setToggles({symOverrideType: v}),
                                   ]}
                            >Use Comment Color</Input>
                        </Panel>
                        <Panel name={'misc'}>
                            {/* these do not affect the main, tbh should probably move these elsewhere. */}
                            <Input {...colorLegend}>Color Legend</Input>
                            <Input
                                type={'checkbox'}
                                state={useMemo(() => [toggles.formatTags, (formatTags: boolean) => setToggles({formatTags})], [setToggles, toggles.formatTags])}
                                name={'style'}
                                title={
                                    outputType === 'Markdown' ?
                                        'Markdown requires underline/italics to distinguish additions/comments' :
                                        'Use underline for additions and italics for comments.'
                                }
                            >Use <u>underline</u> and <i>italics</i></Input>
                            <Input
                                type={'checkbox'}
                                state={[toggles.paragraph, (paragraph: boolean) => setToggles({paragraph})]}
                                name={'paragraph'}
                                title={'Include paragraph symbols with changed newlines'}
                            >Show ¶</Input>
                        </Panel>
                    </Panel>
                </Panel>
                {/* output */}
                <Panel name='export' legend={<h2>Export</h2>}>
                    <Panel name='format'
                           title={"Determine format to export diff in. Formats may not support all formatting options."}>
                        <OutputFormat name="BBCode"
                                      title={"Format compatible with posting on Smogon Forums. Supports all formatting options."}/>
                        <OutputFormat name="Markdown"
                                      title={"Format compatible with posting on Discord. Does not support color, always uses text formatting."}/>
                    </Panel>
                    <textarea name='output' readOnly value={outputCode}/>
                    <button name='copy output' onClick={() => copyToClipboard(outputCode)} disabled={!outputCode}>
                        Copy to Clipboard
                    </button>
                </Panel>
            </div>
        </main>
        <Footer/>
    </div>
}

const useColors = () => {
    const
        useColor = (name: ChangeType) => [name, useLocalStorage<string>(Change.Type.keys[name])] as const,
        entries = [useColor(ChangeType.ADD), useColor(ChangeType.REM), useColor(ChangeType.COM)],
        reset = Object.fromEntries(entries.map(([name, [, , reset]]) => [name, reset]))
    return [
        Object.preventExtensions(
            Object.defineProperties({} as any, entries
                .map(([type, [get, set]]) => ({[type]: {get: () => get ?? defaultColors[type], set}}))
                .reduce((p, c) => Object.assign(p, c))
            ) as ColorMap
        ), useCallback((t?: ChangeType) => {
            t ? reset[t]() : ChangeType.values.forEach(t => reset[t]());
        }, [reset])
    ] as const
}

export default App;
