import 'diff'
import {ArrayChange, Callback, Diff,} from "diff";
import {Change} from "./types";
import Type = Change.Type;

class GPDiff extends Diff {

    // overrides Change.value type.
    // @ts-ignore
    diff(oldString: string, newString: string, callback?: (err: unknown, changes?: Change[]) => void) {
        const process = (err: unknown, changes?: ArrayChange<string>[]) => {
            changes &&= this.transform(changes.map(c => new Change(c)))
            if (callback) callback(err, changes as Change[] | undefined);
            else if (err) throw err;
            return changes as Change[] | undefined;
        }
        const diff = super.diff(oldString, newString, callback && process as Callback) as unknown as Change[]
        return callback ? undefined : process(undefined, diff)
    }

    private* commentIterator(i: number): Generator<Change['value'] | number, undefined, number> {
        let pos = 0;
        for (const [index, comment] of this.comments[i].entries()) {
            while (pos !== index) pos = yield index;
            pos = yield comment;
        }
    }

    private transform(changes: Change[]) {
        {
            // remove all lone spaces
            const accum: typeof changes = []
            // combine all generic spaces into existing changes
            while (changes.length) {
                let border = 0
                const space = changes.findLastIndex((c, index) => {
                    if (!c.type) {
                        if (c.value.at(-1) === ' ') return true;
                        border = index;
                    }
                    return false;
                });
                // working area
                if (space === -1) break;
                const split = changes.splice(space);
                let spacer = space !== -1 ? split.shift() : undefined;
                // distribute space across changes as needed.
                const extra = border ? split.splice(border - space - 1) : [];
                for (const c of split) c.value.unshift(' ');
                if (spacer) {
                    spacer.value.pop();
                    if (spacer.value.length) split.unshift(spacer);
                }
                accum.unshift(...split, ...extra);
            }
            changes.push(...accum);
        }
        if(this.comments[0]?.size) changes = Array.from(function* ( commentGenerator) {
            let count = 0, goal = 0;
            let value: ReturnType<typeof commentGenerator.next>['value'] = 0;
            while (true) {
                const change = changes.shift();
                if (!change) {
                    while (typeof value === "number") {
                        ({value} = commentGenerator.next(goal = value));
                    }
                    if (typeof value !== "undefined") {
                        yield new Change(value, Type.COM)
                        value = 0;
                    } else return;
                } else if (!change.isSpace && !change.removed) {
                    // group spaces with an actual character, since spaces weren't considered in the initial parsing.
                    let trailingSpace: ' '[] = [];
                    const val = Array.from(function* ({value}, changes) {
                        for (let i = 0; i < value.length; i++) {
                            const start = i;
                            // include leading space
                            if (value[i] === ' ') {
                                // trailing space. It shouldn't be here, but it's here until I reorder some operations.
                                if (!value[i + 1]) {
                                    trailingSpace.push(value.pop() as ' ');
                                    continue;
                                }
                                // otherwise group the space with the next token
                                ++i;
                            }
                            yield value.slice(start, i + 1);
                        }
                    }(change, changes));
                    const {length} = val;
                    // non-space count of this token. also might as well do this lol.
                    let start = 0;
                    const next = count + length;
                    if (value === undefined) value = next;
                    while (true) {
                        // noinspection SuspiciousTypeOfGuard
                        while (typeof value === 'number') ({value} = commentGenerator.next(goal = value));
                        if (start === length) break;
                        // if goal is greater than what's in this change, yield the rest and move on
                        let bound = value === undefined ? length : Math.min(Math.max(goal - count, start), length);
                        // yield part before comment, or remainder
                        const last = goal >= next || typeof value === 'undefined';
                        const yielded = start === 0 && bound === length
                            ? change
                            : change.clone({value: val.slice(start, start = bound).flat()})
                        if (last && trailingSpace[0]) yielded.push(' '); // re-add trailing space
                        if (yielded.value.length) yield yielded;
                        if (last) break; // move to next part.
                        // move to next part if the goal isn't here.
                        yield new Change(value!, Type.COM); // yield comment
                        value = Infinity;
                    }
                    count = next; // update count
                } else {
                    yield change;
                }
            }
        }(this.commentIterator(0)))
        {
            // capitalization
            const EXTRA = 1;
            for (let i = changes.length - 1; i >= EXTRA; i--) if (changes[i - 1].removed && changes[i].added) {
                const pair = changes.slice(i - 1, i + 1);
                changes.splice(i - 1, 2, ...function* () {
                    let space: [' '] | [];
                    if (pair.every(i => i.hasLeadingSpace)) {
                        space = [' '];
                        for (const i of pair) i.splice(0, 1);
                    } else {
                        space = [];
                    }
                    const values = pair.map(v => [...v.value])
                    // find the first of each that matches
                    const toYieldRem: string[] = []

                    while (true) {
                        const rem = values[0].shift();
                        if (!rem) break;
                        // find next match
                        const count = values[1].findIndex(add =>
                            rem !== add && rem.toLowerCase() === add.toLowerCase());
                        if (count === -1) {
                            toYieldRem.push(rem);
                            continue;
                        }
                        // yield all the unrelated stuff
                        let pushed = false;
                        if (toYieldRem.length) {
                            const value = toYieldRem.splice(0, Infinity, ...space);
                            yield pair[0].clone({value});
                            pushed = true;
                        }
                        if (count) {
                            const value = values[1].splice(0, count, ...space);
                            yield pair[1].clone({value})
                            pushed = true;
                        }
                        if (pushed) space = [];
                        let add = values[1].shift()!;
                        const remA = [...rem];
                        // split into capitalization/not capitalization
                        // this may have weird behavior when ending with a change
                        while (add) for (let i = 0; i <= 1; i++) {
                            let count: number;
                            for (count = 0; count < remA.length; count++) if (i ^ +(remA[count] !== add[count])) break;
                            if (!count) continue;
                            const value = [add.slice(0, count)], type = i ? Type.ADD : null;
                            if (!type && count === 1 && value[0] === ' ') {
                                space = [' '];
                            } else {
                                if (space.length) value.unshift(space.pop()!);
                                yield new Change(value, type, true);
                            }
                            add = add.slice(count);
                            remA.splice(0, count);
                        }
                    }
                    yield* [toYieldRem, values[1]]
                        .map((value, index) => {
                            if (value.length) value.unshift(...space);
                            return pair[index].clone({value});
                        }).filter(v => v.value.length)
                }());
            }
        }
        // RAR / ARA
        {
            let i = changes.length;
            // the first time splitting happens, we want to consider the very end.
            let useOffset = false;
            const isSym = /^[^\w\s]$/
            merging: while (--i >= 2) {
                // check for two consecutive of the same change fixme should really be caught in the initial space splitting
                if (changes[i - 1].merge(changes[i])) {
                    changes.splice(i, 1);
                    continue;
                }
                {
                    /* fixme make a decision on whether it's okay to mutate individual changes directly. */
                    const USED = 4;
                    if (i >= USED - 1) {
                        const canUseOffset = useOffset && i >= USED;
                        do {
                            let S = i + +!canUseOffset;
                            let cur = changes.slice(S - USED, S);
                            const content = cur.slice(1, 3);
                            if (cur[0].type || !(
                                content.some(c => c.removed) && content.some(c => c.added)
                            )) continue;
                            if (!cur[0].hasLeadingSpace && S - USED > 0) {
                                // banning it from conflicting with capitalization
                                // fixme it should just work instead :/
                                const prev = changes[S - USED - 1];
                                if (prev.added && prev.noMerge) continue;
                            }
                            // todo I wonder if I can just check the two adds/removes instead
                            // todo allow ' ' -> '-' to just be displayed as is.
                            if (
                                // edge case for additions caused by the way the parser works.
                                // code: *W_-W_~ W*~
                                // todo is this too restrictive? there might be a more general case, especially without an offset.
                                useOffset && cur[1].added && cur[2].hasLeadingSpace
                                && cur[1].length === 2 && cur[1][0] === '-'
                                && cur[0].at(-1) === cur[1][1] && cur[1][1] === cur[2][1]
                            ) {
                                cur.pop();
                                let deleteCount = cur.length;
                                const common = cur[0].splice(cur[0].at(-2) === ' ' ? -2 : -1);
                                if (cur[0].length) {
                                    // don't delete it if there were extra parts
                                    S++;
                                    deleteCount--;
                                }
                                cur.shift()
                                // technically this is the same as content at this point
                                cur = cur.map(i => i.clone());
                                // trailing part of the add, if any
                                const suffix = cur[1].length > 2 ? [cur[1].splice(2)] : [];
                                for (const c of cur) c.unshift(...common)
                                // note, the rem should automatically get merged after this.
                                changes.splice(S - USED, deleteCount, ...cur.reverse(), ...suffix)
                            } else if (
                                !cur[3].type &&
                                content.some(c => c.isSpace) &&
                                content.some(([c, ...rest], i) => !rest.length && (i ? c === '-' : isSym.test(c)))
                            ) {
                                let excessSep: Change | undefined;
                                let deleteCount = cur.length;
                                // split off check
                                {
                                    let end = 1;
                                    if (cur[1].at(-2) === ' ') end++;
                                    if (end < cur[0].length) {
                                        excessSep = cur[0];
                                        cur[0] = excessSep.splice(-end);
                                    }
                                }
                                // we only want to split out the innermost tokens of the "same" column
                                const s = S;
                                const [prefix, suffix] = [[0, -1], [3, 0]].map(function ([index, start]) {
                                    if (cur[index].length <= 1) return undefined;
                                    const extract = cur[index].clone()
                                    cur[index] = extract.splice(start, 1);
                                    const toMerge = s + (index ? -(USED + 1) : 1)
                                    if (toMerge >= 0) {
                                        const args = [changes?.[toMerge]]
                                        if (args.every(Boolean)) {
                                            index ? args.push(extract) : args.unshift(extract);
                                            const merged = args[0].merge(args[1]);
                                            if (merged) {
                                                if (!index) return undefined;
                                                else deleteCount++;
                                            }
                                        }
                                    }
                                    return extract;
                                }, S)
                                cur = [Type.REM, Type.ADD]
                                    .map(newType =>
                                        cur.filter(({type}) => !type || type === newType)
                                            .reduceRight((r, l) => {
                                                l = l.clone(newType, true)
                                                l.push(...r);
                                                return l;
                                            }))
                                // S~ ~_-_S => ~S S~ _S-S_
                                // ???
                                cur[1].unshift(' ');
                                const toAdd = [excessSep, prefix, ...cur, suffix].filter(Boolean) as typeof cur
                                changes.splice(S - USED, deleteCount, ...toAdd);
                                // this always results in some being removed, and in small use cases can actually invalidate the loop.
                            } else continue;
                            if (i < changes.length) break;
                            i = changes.length;
                            continue merging;
                        } while (!useOffset && !canUseOffset && (useOffset = true))
                    }
                }

                const sep = changes[i - 1]
                switch (sep.type) {
                    case Type.COM:
                        break;
                    case null: {
                        // todo this might actually be better with an additional offset
                        //R[Punctuation]R
                        if (changes[i - 2].removed && changes[i].removed
                            // match a non-word character
                            && sep.value.length <= 2 && /^[^\w\s]$/.test(sep.value[0])
                            // leading space
                            && (!sep.value[1] || sep.value[1] === ' ')
                        ) {
                            // prevent them from being linked, and if there was no prior space, then none should be added.
                            const [rem, add] =
                                [Change.Type.REM, Change.Type.ADD].map(type => sep.clone({type}))
                            // handle the addition of a change
                            rem.merge(changes[i]);
                            changes[i - 2].merge(rem);
                            const toAdd = [add]
                            if (changes[i + 1]?.added) {
                                if (add.merge(changes[i + 1])) {
                                    changes[i + 1] = add;
                                    toAdd.pop();
                                }
                            }
                            changes.splice(i - 1, 2, ...toAdd)
                            // attempt to remove the gaps
                        }
                        break;
                    }
                    default: {
                        // comments and union changes are already where they should be.
                        if (!changes[i].typeIs(Type.ADD, Type.REM)) break;
                        // don't move changes across punctuation.
                        if (!changes[i - 1].value.every(RegExp.prototype.test, /^(?:\w+| )$/)) break;
                        // merge to get (S|C)(R R)(A A)(S|C)
                        for (let S = 2; S > 0 && changes[i - S].merge(changes[i]); S--) {
                            changes.splice(i, 1);
                        }
                    }
                }
            }
        }
        return changes;
    }

    // stored comments. first index is new, second is old. second is currently unused.
    // key is the nth token in the output.
    readonly comments: Map<number, string[]>[] = [];

    private extractComments(value: string[]) {
        if (this.comments.length > 2) throw new Error('Error: tainted diff object!'); // these are one use.
        this.comments.unshift(new Map())
        const map = this.comments[0];
        // non-space index to prevent space handling from affecting comments
        let tokens = 0;
        for (let start = 0; start < value.length; start++) {
            while (value[start] === '{') {
                const end = value.indexOf('}', start + 1);
                if (end === -1) break;
                // index offset of the comment. includes a leading space
                const [prefix, suffix] = [start, end].map(i => value[i - 1] === ' ' ? 2 : 1);
                // remove start/end bracket and leading space if any
                const comment = value.splice(start -= prefix - 1, end - start + 1)
                    .slice(prefix, -suffix);
                map.set(tokens, map.get(tokens)?.concat(comment) ?? comment);
            }
            if (value[start] !== ' ') tokens++;
        }
        return value;
    }

    removeEmpty = this.extractComments

    // don't join, instead return arrays of tokens
    join = (chars: any) => chars

    // words, but non-word characters are by themselves and limit one of each space character.
    // only space characters processed are space and newline.
    static readonly tokenizer = /\w+| (?=\S)|[^\s\w]|\n/g

    tokenize(value: string) {
        return Array.from(value.trim().matchAll(GPDiff.tokenizer), m => m[0]);
    }
}

export default function gpDiff(oldString: string, newString: string) {
    /*
     TODO
     RULES
             [r][a] [a] => [r][a]                      a space cannot be the common diff between two of the same tag
              []\W.+[/] => []\W[/][].+[/]              punctuation are always by themselves
     [r]1\W[/r][a]2[/a] => [r]1[/r][a]2[/a][r]\W[\r]   punctuation is never included in the middle of a removal or addition
                                                 {.+}  comments
    */
    return new GPDiff().diff(oldString, newString);
}