import React, {HTMLProps, useMemo} from "react";
import {useBoolean} from "react-use";

export function Input<G, S>({
                         state: [get, set],
                         type,
                         title,
                         children,
                         labelProps,
                         ...props
                     }: HTMLProps<HTMLInputElement> & {
    type: React.HTMLInputTypeAttribute,
    state: Readonly<[get: G, set: (v: S) => void]>,
    labelProps?: HTMLProps<HTMLLabelElement>
}) {
    const key = type === 'checkbox' ? 'checked' : 'value';
    if (props[key] !== undefined) props.disabled = true;
    else if (key === 'checked') props[key] = get as boolean
    else props[key] = get as string
    return <label className={'inputLabel'} title={title} {...labelProps}>
        <span>{children}</span>
        <input type={type} onChange={e => set(e.target[key] as S)}{...props}/>
    </label>
}

// this works because checkboxes don't actually care if they're immediately re-rendered.
export function useCheckbox(initial = false) {
    const state = useBoolean(initial);
    return [
        state[0],
        useMemo(
            () => React.memo<Partial<Omit<Parameters<typeof Input>[0], 'state' | 'type'>>>(function Checkbox(props) {
                return <Input type={'checkbox'} {...{...props, state}}/>
            }),
            [state]
        )
    ] as const
}
