import React, {HTMLProps, ReactNode, useCallback} from "react";
import {useBoolean} from "react-use";
import "./Panel.css"
import {toArray} from "./gpdif/types";

export default React.memo<
    HTMLProps<HTMLFieldSetElement> & {
    legend?: ReactNode, name: string, legendProps?: HTMLProps<HTMLLegendElement>,
    onHide?: (hidden: boolean) => void
}>(
    function Panel(
        {
            children, legend, legendProps,
            onHide, hidden = false,
            className, ...props
        }
    ) {
        const {name} = props
        const [hiddenState, toggleHidden] = useBoolean(hidden);
        hidden = hiddenState;
        const hiddenProps = useCallback(function<T extends {title?: string, onClick?: Function}>(props?: T, whenHidden = false) {
            return whenHidden === hidden ? {...props,
                title: `${props?.title ? props.title + '\n\n' : ''}Clicking this will ${hidden ? 'show' : 'hide'} this section.`,
                onClick: (e: any) => {
                    toggleHidden();
                    props?.onClick?.(e);
                }
            } : props;
        }, [hidden, toggleHidden])
        React.useEffect(() => onHide?.(hidden), [onHide, hidden])
        legend ??= name.split(' ').map(i => i[0].toUpperCase() + i.substring(1))

        const asClassName = (...args: Array<string[] | string | false | undefined>) => args
            .flat()
            .concat(className)
            .filter(i => i)
            .join(' ')
        return <fieldset
            className={asClassName('panel')}
            {...hiddenProps(props, true)}>
            <legend {...hiddenProps(legendProps)}>{legend}</legend>
            <div className={asClassName('panel-content')} style={hidden ? {display: 'none'} : undefined}
                 hidden={hidden}>{children}</div>
        </fieldset>
    })