import {useBoolean, useLocalStorage} from "react-use";
import React, {
    DOMAttributes, useCallback,
    useDeferredValue,
    useEffect,
    useMemo,
    useRef, useState, useTransition
} from "react";
import scrollIntoView from "scroll-into-view-if-needed"
import gpDiff from "./gpdif/gpdiff";
import {Change, toArray} from "./gpdif/types";
import {Format} from "./gpdif/diff-format";
import "./Diff.css"
import {HTMLFormat} from "./HTMLFormat";

const tags = ['old', 'new'] as const;
type Tag = typeof tags[number]

export function BoxHeader({title}: { title: string }) {
    return <h2 autoCapitalize='words'>{title}</h2>
};

// generates components needed for diff handling
export function useDiff(toggles?: Format.Toggles) {
    const
        hooks: Record<Tag, ReturnType<typeof useLocalStorage<string>>> = {
            old: useLocalStorage('old'),
            new: useLocalStorage('new'),
        },
        reset = Object.fromEntries(
            Object.entries(hooks)
                .map(([id, [, , reset]]) => [id, reset])
        ),
        states = Object.fromEntries(
            Object.entries(hooks).map(([id, [get, set]]) =>
                [id as Tag, [get, set]] as const)
        ),

        text: Record<Tag, string> = Object.preventExtensions(
            Object.defineProperties({} as any, Object.entries(states)
                .map(([type, [get = '', set]]) => ({[type]: {get: () => get, set}}))
                .reduce((p, c) => Object.assign(p, c))
            )),
        // diff parsing
        // fixme this isn't reversible after formatting. If it was, a lot of post-formatting computation would be way easier.
        [valueColorLegend, colorLegend] = useColorLegend(),
        [trueDiff = [], setTrueDiff] = useState<ReturnType<typeof gpDiff>>(),
        [isPending, startTransition] = useTransition();
    useEffect(() => startTransition(() => setTrueDiff(
        toArray(gpDiff(text.old, text.new))
    )), [text.old, text.new]);
    // mirrors trueDiff, but with the color functionality built-in too.
    const [diff, setDiff] = useState(trueDiff);
    useEffect(() =>
        startTransition(() => setDiff(
            trueDiff && valueColorLegend ?
                (
                    ([
                        [['add'], Change.Type.ADD],
                        [[' ', 'remove'], Change.Type.REM],
                        [['comment'], Change.Type.COM],
                        [['\n\n']],
                    ] satisfies Array<[value: string[], type?: Change.Type]>).map(([value, type = null]) =>
                        new Change(value, type)
                    ) as typeof trueDiff
                ).concat(trueDiff) : trueDiff
        )), [trueDiff, valueColorLegend])
    // todo handle overrides as well.
    const
        formatter = useMemo(() => new HTMLFormat(toggles), [toggles]),
        // swap the contents of the boxes
        swap = Object.assign(
            useCallback(function swapBoxes() {
                [text.old, text.new] = [text.new, text.old]
            }, [text]),
            {
                disabled: text.old === text.new,
                title: "swap the content of the two boxes"
            },
        ),
        clear = Object.assign(
            useCallback(function clear(tag?: Tag) {
                if (tag) reset[tag]();
                else tags.forEach(clear);
            }, [reset]),
            {
                disabled: !(text.old || text.new)
            }
        );
    return {diff, text, formatter, isPending, hideOld: useBoolean(false), swap, clear, colorLegend} as const
}


export namespace Versions {
    export interface Props extends Omit<DOMAttributes<HTMLDivElement>,
        'onPasteCapture' | 'onCopy' | 'children'
    > {
        useDiff: ReturnType<typeof useDiff>,
        export?: (changes: Props["useDiff"]["diff"]) => string
    }
}

export function Versions(
    {
        export: formatAsExport, useDiff: {text, diff, hideOld: [oldHidden, hideOld], formatter, isPending,}, ...props
    }: Versions.Props
) {
    const refs = {
        boxes: {
            old: useRef<HTMLTextAreaElement>(null),
            new: useRef<HTMLTextAreaElement>(null),
        },
        output: useRef<HTMLOutputElement>(null),
    }
    const changesRef = React.useRef<typeof formatter.changes>();
    const
        _preview = useMemo(() => formatter.format(diff!), [formatter, diff]),
        preview = useDeferredValue(_preview);
    isPending ||= preview !== _preview; // prevent formatting from delaying the program
    // todo auto-select?
    useEffect(() => {
        const changes = changesRef.current;
        if (changes === formatter.changes) return;
        const scrollIfNeeded = scrollIntoView.bind({options: {scrollMode: 'if-needed'}})
        const output = refs.output?.current;
        if (changes && formatter.changes) for (let i = 0; true; i++) {
            const [last, curr] = [changes, formatter.changes].map(c => c.at(i));
            if (!last && !curr) break;
            const children = [last, curr].map(i => i && (i.props.children as Iterable<React.ReactNode>)[Symbol.iterator]());
            let toScroll = output!.children!.item(i);
            if (last?.key === curr?.key) {
                if (children.includes(undefined)) continue;
                // find first deviation
                let j = -1;
                let values: IteratorResult<React.ReactNode>[], done: boolean;
                do {
                    values = children.map(i => i!.next())
                    done = values.every(v => v.done);
                    j++;
                } while (!done && values[0].value === values[1].value)
                if (done) continue;
                // hmm
                const node = toScroll?.childNodes[j];
                if (node) {
                    // can't select it directly :/
                    const range = new Range();
                    range.selectNode(node);
                    const span = document.createElement("span")
                    range.insertNode(span);
                    scrollIfNeeded(span);
                    span.remove();
                    // todo maybe it would be nice to select the area? previous attempts have been inconsistent for some reason
                    break;
                }
            }
            toScroll ||= output?.lastElementChild || null;
            if (toScroll) scrollIfNeeded(toScroll);
            break;
        }
    }, [formatter.changes, preview, refs.output])

    useEffect(() => {
        changesRef.current = formatter.changes
    }, [formatter.changes])

    const Version = (id: Tag) => {
        const t = text[id];
        return <label className={'version boxes'} key={id}>
            <BoxHeader title={id}/>
            <textarea name={id}
                      ref={refs.boxes[id]}
                      value={text[id]}
                      onInput={({currentTarget: {value}}) => text[id] = value}
            />
            <div className={'wordCount'}>Characters: {useMemo(() =>
                t.replaceAll(/\s|\{.*?}/gs, '').length,
                [t],
            )}</div>
        </label>
    }
    return <div className='versions'
                onPasteCapture={e => {
                    const boxes = Object.values(refs.boxes).map(i => i.current);
                    const index = boxes.indexOf(e.target as any);
                    const source = index === -1 ? null : boxes[index];
                    if (source && (source.selectionStart || source.selectionEnd !== source.textLength)) return; // non-full paste should be handled as default
                    const other = source && boxes[+!index]
                    if (!(other ? other.textLength : text.new || text.old) || window.confirm('Overwrite both boxes?')) {
                        text.new = text.old = e.clipboardData.getData('text/plain');
                        e.preventDefault();
                        e.stopPropagation();
                        hideOld(true);
                    }
                }}
                {...props}>
        <div hidden={oldHidden} style={!oldHidden ? {display: 'contents'} : undefined}>{Version('old')}</div>
        <label className='boxes output'>
            <BoxHeader title='Output'></BoxHeader>
            <div className={'lockSize diff-container'}>
                {/* fixme I would like it to be able to contract if diff doesn't take up the full space. I just don't want the diff preview to be able to expand on its own */}
                <output className='diff' name='diff'
                        ref={refs.output}
                        style={
                            isPending ? {opacity: 0.5, transition: 'opacity 0.05s 0.2s linear', cursor: 'progress'} :
                                undefined
                        }
                        onCopy={formatAsExport && (event => {
                            event.preventDefault(); // prevent inconsistent behavior
                            const changes = changesRef.current
                            if (!changes) return;
                            const selection = document.getSelection();
                            if (!selection) return;
                            let toReturn: Change[] = []
                            let capturing = false;
                            for (let i = 0; i < changes.length; i++) {
                                const e = event.currentTarget.children.item(i)!;
                                const type = changes[i].key
                                        ?.match(new RegExp((Change.Type.values as readonly string[]).reduce((p, c) => `${p}|${c}`)))
                                        ?.[0] as Change.Type | undefined
                                    ?? null
                                let value: string[]
                                if (selection.containsNode(e)) {
                                    value = changes[i].props.children as string[]
                                } else if (selection.containsNode(e, true)) {
                                    value = [];
                                    for (const node of e.childNodes) if (node.textContent && selection.containsNode(node)) {
                                        value.push(node.textContent);
                                    }
                                } else if (capturing) break; else continue;
                                capturing = true;
                                const change = new Change(
                                    value,
                                    type ?? null,
                                    /** spacing should be left as is **/ true,
                                );
                                if (formatter.symbols && change.value[0]?.match(/^\W$/) && change.value[1]?.match(/^ \([AR]\w\)/)) {
                                    // symbols always match this pattern
                                    change.value.splice(1);
                                }
                                toReturn.push(change);
                            }
                            // fixme this is broken with punctuation. Maybe I should filter out symbols? possibly splitting symbol changes would be good here.
                            if (toReturn.length) event.clipboardData.setData('text/plain', formatAsExport(toReturn))
                        })}
                >{preview}</output>
            </div>
        </label>
        {Version('new')}
    </div>
}

// most information relating to showing data about the color legend feature.
function useColorLegend() {
    const
        name = "color_legend",
        [checked, setChecked] = useState<boolean>(),
        [get, set] = useLocalStorage<boolean>(name),
        value = checked ?? get;
    const props = ({
        name,
        type: 'checkbox',
        title: checked === undefined ? 'display a color legend at the beginning of output' : "This output format does not support color",
        state: [get, set],
    }) as const
    return [
        value,
        Object.defineProperty(props, 'checked', useMemo(() => ({
            get: () => checked,
            set(v?: boolean) {
                v !== checked && setChecked(v)
            },
            enumerable: true
        }) as const, [checked])) as typeof props & { checked?: boolean }
    ] as const;
}