import {Format} from "./gpdif/diff-format";
import {Change, toArray} from "./gpdif/types";
import React, {Key} from "react";
import "./HTMLFormat.css"

export class HTMLFormat extends Format<HTMLFormat.TagFun | string, HTMLFormat.Child> {

    [Change.Type.ADD] = 'ins';
    [Change.Type.REM] = 'del';
    [Change.Type.COM] = 'i';
    private default = 'span';

    public changes: HTMLFormat.Changes = []

    private resetChangeCount = () => Object.fromEntries([0, ...Change.Type.values].map(t => [t, 0])) as Record<Change.Type | 0, number>;
    private _changeCount: ReturnType<typeof this.resetChangeCount> | null = null;
    public get changeCount() {
        return this._changeCount ??= this.resetChangeCount()
    }

    format(...args: Parameters<HTMLFormat.Super['format']>) {
        this._changeCount = null;
        const res = super.format(...args);
        this.changes = res.map(({type, key, props}) => ({type, key, props}))
        return res;
    }

    override formatChange({value}: Change, Tag: ReturnType<typeof this.formatTag>) {
        return Tag({children: value})
    }


    override formatTag(t?: Change.Type, formatTags?: boolean): HTMLFormat.TagFun {
        // note, the input tags are always strings
        const type = super.formatTag(t, formatTags) as string ?? this.default;
        return ({id, style, className, key, ...props}) => {
            if (!key) {
                const k = t ?? 0;
                key = `${k as any + this.changeCount[k]++}`
            }
            if (t) {
                if (this.colors) style = {color: this.colors[t], ...(style || {})}
                if (t) className = toArray(className).concat(t, 'change').join(' ')
            }
            return React.createElement(type, {...props, id, key, className, style})
        }
    }
}

export module HTMLFormat {
    export type Changes = Array<Pick<ReturnType<HTMLFormat['formatChange']>, 'type' | 'key' | 'props'>>
    export type TagProps<T = any> =
        Pick<React.HTMLAttributes<T>, 'id' | 'style' | 'className' | 'children'>
        & { key?: Key }
    export type TagFun = <T extends Element = Element>(props: TagProps) => React.DOMElement<typeof props, T>
    export type Child = ReturnType<TagFun>
    export type Super = Format<TagFun | string, Child>
}