import {Format} from "./gpdif/diff-format";
import {useDeepCompareEffect, useLocalStorage} from "react-use";
import {useState} from "react";

class State extends Format.Toggles {
    symbols = {
        enabled: true,
        replaceAtEnds: true,
        omitRemoved: true,
        overrideType: false,
    }
}

const defaults = new State()

const {symbols: defaultSymbols, ...defaultState} = defaults

const aliases: Record<string, string> = {}

// fixme ... just everything. I want to store it between sessions, but I can't figure out a clean way to do that... this renders way too many times, and it should be moved to an actual reducer
// the current way spams rerender over and over again, it shouldn't have to.
// and if I threw it all into the same, for whatever reason it can interfere with itself...
export function useToggles(colors: Format.Toggles["colors"]) {
    type StateKeys = keyof (Omit<State, 'symbols' | 'colors'> & State['symbols']) & string

    function useProp<K extends StateKeys>(key: K, alias?: string) {
        const value = key in defaultSymbols ? defaultSymbols[key as keyof typeof defaultSymbols] : defaultState[key as keyof typeof defaultState]
        alias ??= key in defaults.symbols ? `sym${key[0].toUpperCase()}${key.slice(1)}` : key
        aliases[alias] = key
        return {[key]: useLocalStorage(alias, value as boolean)} as const
    }

    const propStates = ([
        useProp('paragraph'),
        useProp('formatTags'),
        useProp('enabled', 'symbols'),
        useProp('replaceAtEnds'),
        useProp('omitRemoved'),
        useProp('overrideType'),
    ] as const).reduce((p, q) => Object.assign(p, q), {}) as Record<StateKeys, ReturnType<typeof useProp>[string]>;
    const [propGets, propSets] = [
        Object.fromEntries(Object.entries(propStates).map(([k, [get]]) => [k, get])) as
            Record<StateKeys, boolean | undefined>,
        Object.fromEntries(Object.entries(propStates).map(([k, [, set]]) => [k, set] as const))
    ]

    // for whatever reason this never seems to render properly if I memoize it.
    // which is a damn shame, since this really contributes to the amount of renders.
    const
        values = Object.entries(propGets),
        syms = values.filter(([k]) => k in defaults.symbols),
        base = Object.fromEntries(values.filter(([k]) => !(k in syms))),
        symbols = syms.length ? Object.fromEntries(syms) : undefined
    const res = {...base, symbols: !symbols || symbols.enabled ? symbols : null, colors}
    const createReducer = () => (change: Record<string, boolean>) => Object.entries(change)
            .forEach(([k, v]) => propSets[aliases[k] as StateKeys](v)),
        [reducer, setReducer] = useState(createReducer);
    useDeepCompareEffect(() => setReducer(createReducer), [propSets])
    return [res as Format.Toggles, reducer] as const
}